import React, { Fragment, useState } from 'react'
import { Link, graphql } from 'gatsby'
import styled from 'styled-components'

import Bio from '../components/bio'
import Layout from '../components/layout'
import SEO from '../components/seo'

const PasswordPage = styled.div`
  width: 100vw;
  margin: auto;
  height: 100vh;
  display: flex;
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);;
  align-items: center;;
  & > input {
    width: 100%;
    max-width: 1000px;
    margin: auto;
    border: 0;
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    height: 5em;
    text-align: center;
    font-size: 4em;
    outline: none !important;
    box-shadow: 0 0 50px #fdfdfd;
    &:focus {
      outline-color: transparent;
    }
  }
`

const PostHeader = styled.section`
  height: 380px;
  padding: 75px;
  background-image: url('/city.jpeg');
  background-position: bottom;
  background-size: cover;
  display: flex;
  justify-content: center;
  & > p {
    margin-top: 220px;
  }
  h1,
  h2 {
    margin: 0;
    padding: 0;
    text-align: right;
    color: white;
  }
  h1 {
    font-size: minmax(4em, 4vw);
    font-weight: 300;
    text-shadow: 0 0 50px black;
  }
`

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes
  const [password, setPassword] = useState('')

  if (posts.length === 0) {
    return (
      <Layout location={location} title={siteTitle}>
        <SEO title='MOIN 모인 기술 블로그' />
        <Bio />
        <p>
          No blog posts found. Add markdown posts to "content/blog" (or the directory you specified for the
          "gatsby-source-filesystem" plugin in gatsby-config.js).
        </p>
      </Layout>
    )
  }

  return password === 'ilovemoin1' || password === 'ㅑㅣㅐㅍ드ㅐㅑㅜ1' ? (
    <Fragment>
      <PostHeader>
        <p>
          <h1>MOIN 기술 블로그</h1>
        </p>
      </PostHeader>
      <Layout location={location} title={siteTitle}>
        <SEO title='All posts' />
        {/* <Bio /> */}

        <ol style={{ listStyle: `none` }}>
          {posts.map(post => {
            const title = post.frontmatter.title || post.fields.slug

            return (
              <li key={post.fields.slug}>
                <Link to={post.fields.slug} itemProp='url'>
                  <article className='post-list-item' itemScope itemType='http://schema.org/Article'>
                    <header>
                      <h2>
                        <span itemProp='headline'>{title}</span>
                      </h2>
                      <small>{post.frontmatter.date}</small>
                    </header>
                    <section>
                      <p
                        dangerouslySetInnerHTML={{
                          __html: post.frontmatter.description || post.excerpt,
                        }}
                        itemProp='description'
                      />
                    </section>
                  </article>
                </Link>
              </li>
            )
          })}
        </ol>
      </Layout>
    </Fragment>
  ) : (
    <PasswordPage>
      <input autoFocus type='password' value={password} onChange={(e) => setPassword(e.target.value)} placeholder={'개발중입니다.'} />
    </PasswordPage>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
